import React, { Component } from "react";
import Studentpop from "./students";
import Supervisorpop from "./supervisors";
import Sponsorpop from "./sponsorspop";
import "./team.css";

class Team extends Component {
  state = {
    seenStudent: false,
    seenSupervisor: false,
  };
  togglePopStudent = () => {
    this.setState({
      seenStudent: !this.state.seenStudent,
    });
  };
  togglePopSupervisor = () => {
    this.setState({
      seenSupervisor: !this.state.seenSupervisor,
    });
  };
  togglePopSponsor = () => {
    this.setState({
      seenSponsor: !this.state.seenSponsor,
    });
  };

  render() {
    return (
      <main className="team-lecchain">
        <div className="left-panel">
          <div className="left-panel-btn" onClick={this.togglePopStudent}>
            <img
              height="75px"
              src="data:image/svg+xml;base64,"
              alt="students-logo"
            />
            STUDENTS
          </div>
          <div className="left-panel-btn" onClick={this.togglePopSupervisor}>
            <img
              height="75px"
              alt="supervisors-logo"
              src="data:image/svg+xml;base64,"
            />
            SUPERVISORS
          </div>
          <div className="left-panel-btn" onClick={this.togglePopSponsor}>
            <img
              height="75px"
              src="data:image/svg+xml;base64,"
              alt="sponsors-logo"
            />
            SPONSORS
          </div>
        </div>
        <div className="right-panel">
          <h2 className="team-title">TEAM</h2>
          <div className="team-introduction">
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Et quasi
            vel tenetur officia cum, inventore ex sint aperiam hic pariatur
            suscipit perspiciatis dolores deleniti dolorum, maiores similique
            cupiditate doloremque iste.
          </div>
          <div className="team-recomendation">
            Lorem, ipsum dolor sit amet consectetur adipisicing elit.
          </div>
        </div>
        {this.state.seenStudent ? (
          <Studentpop toggle={this.togglePopStudent} />
        ) : null}
        {this.state.seenSupervisor ? (
          <Supervisorpop toggle={this.togglePopSupervisor} />
        ) : null}
        {this.state.seenSponsor ? (
          <Sponsorpop toggle={this.togglePopSponsor} />
        ) : null}
      </main>
    );
  }
}

export default Team;
